import React, { Component } from 'react'

import fleet from './../../argus website/PNG/fleet linked in.png'

class Contact extends Component {
    

    constructor(props) {
        super(props)
    
        this.state = {
             name: '',
             phone: '',
             message: ''
        }
    }

    handleNameChange = (event) => {
        this.setState({
            name: event.target.value
        })

    }

    handlePhoneChange = (event) => {
        this.setState({
            phone: event.target.value
        })

    }

    handleMessageChange = (event) => {
        this.setState({
            message: event.target.value
        })

    }

    handleSubmit = (event) => {
        alert(`${this.state.name} ${this.state.phone} ${this.state.message}`)
        event.preventDefault()
    }
    
    
    render() {
        return (
            <div>
                <div className="text-gray-600 body-font bg-no-repeat bg-cover bg-jobsbg">
                    <div className="container mx-auto flex px-5 py-40 items-center justify-center flex-col">
                        <div className="text-center lg:w-2/3 w-full">
                            <h1 className="title-font sm:text-7xl text-3xl mb-4 font-medium text-white">Contact</h1>
                        </div>
                    </div>
                </div>

                <div className="overflow-hidden bg-no-repeat bg-mapbg">
                    <div className="container px-4 py-6 mx-auto sm:px-20 mb:px-26">
                        <img src={fleet} alt="Security Cars"/>
                        <div className="flex flex-wrap my-12">
                            <div className="p-2 md:w-1/4 flex flex-col items-start text-l font-medium text-gray-2">
                                <div class="flex flex-col sm:flex-row items-center w-full">
                                    <hr class="border-2 border-red-1 w-2/12 lg:w-2/12 mb-2 lg:m-4"/>
                                    <h1 class="text-4xl title-font font-bold text-gray-900 mt-4 mb-4 lg:w-10/12">How can we help?</h1>
                                </div>
                                <p className="leading-relaxed text-l font-medium text-gray-2 mb-8">We are experiencing longer than usual response
                                times due to Covid-19. We appreciate your
                                patience and apologize in advance for any delays
                                in responding to your message. </p>
                                <hr class="border-1 border-gray-600 w-11/12 my-3" />
                                <a href="tel:6472891070" class="px-2">
                                    {/* <img src={phone_logo} alt="Phone Logo" /> */}
                                    <p>647-289-1070</p>
                                </a>
                                <a href="mailto:info@argussecurityservices.ca" class="px-2">
                                    {/* <img src={phone_logo} alt="Phone Logo" /> */}
                                    <p>info@argussecurityservices.ca</p>
                                </a>
                                {/* <img src={phone_logo} alt="Phone Logo" /> */}
                                <p>350 Rutherford Road South Brampton ON L6W-4N6 Suite 210 Plaza 2</p>
                            </div>
                            <form className="bg-gray-200 p-6" onSubmit={this.handleSubmit}>
                                <input className="w-full mb-3 py-5 px-4 focus:outline-none focus:ring-1 ring-red-1" type="name" placeholder="Your Name" value={this.state.name} onChange={this.handleNameChange}/>
                                <input className="w-full mb-3 py-5 px-4 focus:outline-none focus:ring-1 ring-red-1" type="telephone" placeholder="Phone Number" value={this.state.phone} onChange={this.handlePhoneChange}/>
                                <textarea className="w-full h-56 mb-3 py-5 px-4 focus:outline-none focus:ring-1 ring-red-1" type="text" placeholder="Write Message" value={this.state.message} onChange={this.handleMessageChange}/>
                                <button className="w-full p-4 rounded-lg border text-white bg-red-1 hover:bg-white hover:text-red-1 hover:border-red-1">SEND MESSAGE</button>
                            </form>
                        </div>
                    </div>
                </div>


                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2885.38568994079!2d-79.71944568499285!3d43.681744458603305!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x882b3f742bd5dfaf%3A0x4e85dd4aa00d79f1!2sArgus%20Security%20Services%20Corp.!5e0!3m2!1sen!2sin!4v1628009453160!5m2!1sen!2sin" className="w-full h-80 bg-gray-200" allowfullscreen="" loading="lazy"></iframe>

            </div>
        )
    }
}

export default Contact

